import React, { useCallback, useEffect, useMemo } from "react";
import PropTypes from "prop-types";
import { Box, Skeleton, Typography } from "@mui/material";

import { useState } from "react";
import PaginationComponent from "../../../components/Pagination";
import TableUser from "../components/TableUser";
import DehazeIcon from "@mui/icons-material/Dehaze";
import FormAddUser from "../components/FormAddUser";
import userApi from "../../../api/userApi";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import querystring from "query-string";
import { useSnackbar } from "notistack";
import DialogDelete from "../components/DialogDelete";
import DialogEditUser from "../components/DialogEditUser";


const QuanlyTaikhoan = (props) => {
  const [loading, setLoading] = useState(true);
  const [userList, setUserList] = useState([]);
  const [pagination, setPagination] = useState({
    page: 1,
    total: 1,
  });

  const [openDialogDelete, setOpenDialogDelete] = useState({
    status: false,
    id_Delete: null,
  });

  const navigate = useNavigate()

  const [openDialogEditUser, setOpenDialogEditUser] = useState({
    status: false,
    user: null,
  });

  const location = useLocation();
  // const navigate = useNavigate();
  let [searchParams, setSearchParams] = useSearchParams();
  const { enqueueSnackbar } = useSnackbar();

  const queryParams = useMemo(() => {
    const params = querystring.parse(location.search);
    return {
      ...params,
      page: Number(params.page) || 1,
    };
  }, [location.search]);

  const handleCloseDialogDelete = () => {
    setOpenDialogDelete({
      ...openDialogDelete,
      status: false,
    });
  };

  const handleOpenDialogDelete = (id) => {
    setOpenDialogDelete({
      status: true,
      id_Delete: id,
    });
  };

  const handleCloseDialogEditUser = () => {
    setOpenDialogEditUser({
      ...openDialogEditUser,
      status: false,
    });
  };

  const handleOpenDialogEditUser = (user) => {
    setOpenDialogEditUser({
      user,
      status: true,
    });
  };

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await userApi.getUsers(queryParams.page);
        setUserList(res.data.users);
        setPagination({
          page: Number(res.data.page),
          total: res.data.total,
        });
        setLoading(false);
      
      } catch (error) {
        if(error.message === "Token không hợp lệ vui lòng đăng nhập hoặc đã hết hạn. Vui lòng đăng nhập lại"){
          navigate('/login');
        };
          console.log(error.message)
          enqueueSnackbar(error.message,{
            anchorOrigin: {
              vertical: 'bottom',
              horizontal: 'right'
            },
             variant: 'error' 
          });
      }
    };

    fetchUsers();
  }, [queryParams]);

  const handleChangePage = (value) => {
    setPagination({
      ...pagination,
      page: value,
    });

    const newFilters = {
      ...queryParams,
      page: value,
    };

    setSearchParams(newFilters);
  };

  const handleSubmitAddUser = async (data) => {
    try {
      let res = await userApi.addUser(data);

      setUserList(res.data.users);
      setPagination({
        page: 1,
        total: res.data.total,
      });

      const newFilters = {
        ...queryParams,
        page: 1,
      };

      setSearchParams(newFilters);

      enqueueSnackbar(res.data.message, {
        anchorOrigin: {
          vertical: "bottom",
          horizontal: "right",
        },
        variant: "success",
      });
    } catch (error) {
      enqueueSnackbar("Có lỗi xảy ra khi thêm mới tài khoản", {
        anchorOrigin: {
          vertical: "bottom",
          horizontal: "right",
        },
        variant: "error",
      });
    }
  };

  const handleSubmitEditUser = async (values) => {
    let page = pagination.page;
    const obj= {...values, page}
    try {
      let res = await userApi.editUser(obj);
      setUserList(res.data.users);

      enqueueSnackbar(res.data.message, {
        anchorOrigin: {
          vertical: "bottom",
          horizontal: "right",
        },
        variant: "success",
      });

    } catch (error) {
      if(error.message === "Token không hợp lệ vui lòng đăng nhập hoặc đã hết hạn. Vui lòng đăng nhập lại"){
        navigate('/login');
      };

      enqueueSnackbar(error.message, {
        anchorOrigin: {
          vertical: "bottom",
          horizontal: "right",
        },
        variant: "error",
      });
    }
  };

  const handleConfirmDelete = async () => {
    try {
      let res = await userApi.deleteUser(openDialogDelete.id_Delete);
      setUserList(res.data.users);
      setPagination({
        ...pagination,
        page: 1,
        total: res.data.total,
      });

      const newFilters = {
        ...queryParams,
        page: 1,
      };

      setSearchParams(newFilters);

      setOpenDialogDelete({
        ...openDialogDelete,
        status: false,
      });

      enqueueSnackbar(res.data.message, {
        anchorOrigin: {
          vertical: "bottom",
          horizontal: "right",
        },
        variant: "success",
      });
    } catch (error) {
      if(error.message === "Token không hợp lệ vui lòng đăng nhập hoặc đã hết hạn. Vui lòng đăng nhập lại"){
        navigate('/login');
      };
      enqueueSnackbar(error.message, {
        anchorOrigin: {
          vertical: "bottom",
          horizontal: "right",
        },
        variant: "error",
      });
    }
  };

  const handleCancelDelete = () => {
    setOpenDialogDelete({
      ...openDialogDelete,
      status: false,
    });
  };

  return (
    <Box style={{ padding: "24px 12px" }}>
      <Box style={{ display: "flex", alignItems: "center" }}>
        <DehazeIcon style={{ marginRight: "12px" }} />
        <Typography variant="h5">Quản trị tài khoản hệ thống</Typography>
      </Box>

      <FormAddUser onSubmit={handleSubmitAddUser} />

      {loading ? (
        <Skeleton variant="rectangular" width="100%" height="450px" />
      ) : (
        <TableUser
          userList={userList}
          page={pagination.page}
          onClickOpenDialogDelete={handleOpenDialogDelete}
          onClickOpenDialogEditUser={handleOpenDialogEditUser}
        />
      )}

      <DialogDelete
        open={openDialogDelete.status}
        onCloseDialogDelete={handleCloseDialogDelete}
        onConfirmDelete={handleConfirmDelete}
        onCancelDelete={handleCancelDelete}
      />

      <DialogEditUser
        open={openDialogEditUser.status}
        user={openDialogEditUser.user}
        onCloseDialogEditUser={handleCloseDialogEditUser}
        onSubmit={handleSubmitEditUser}
      />

      <PaginationComponent
        page={pagination.page}
        totalPage={pagination.total}
        onChangePage={handleChangePage}
      />
    </Box>
  );
};

export default QuanlyTaikhoan;
