import React from "react";
import PropTypes from "prop-types";
import { Box, Button, Grid, LinearProgress, Typography } from "@mui/material";
import { InputField } from "../../../components/form-control/InputField";
import * as yup from "yup";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { IconButton } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";

const schema = yup
  .object({
    username: yup
      .string()
      .required("Lỗi: Vui lòng nhập tên tài khoản")
      .min(5, "Lỗi: Tên tài khoản phải nhiều hơn 5 kí tự"),
    password: yup.string().required("Lỗi: Vui lòng nhập mật khẩu"),
    thutu: yup
      .number()
      .required("Lỗi: Vui lòng nhập thứ tự")
      .min(1, "Thứ tự phải lớn hơn 1"),
  })
  .required();

const FormAddUser = ({ onSubmit }) => {
  const form = useForm({
    defaultValues: {
      username: "",
      password: "",
      thutu: 1,
    },
    resolver: yupResolver(schema),
  });

  const handleFormSubmit = async (values) => {
    if (onSubmit) {
      await onSubmit(values);
      form.reset();
    }
  };

  const { isSubmitting } = form.formState;

  return (
    <Box mt={2}>
      {isSubmitting ? <LinearProgress /> : ""}
      <form
        onSubmit={form.handleSubmit(handleFormSubmit)}
        style={{ width: "100%" }}
      >
        <fieldset style={{border: "1px solid #ddd",paddingBlockEnd: "12px"}}>
          <legend style={{paddingInline: "12px"}}>Thêm mới tài khoản:</legend>
          <Grid
            container
            spacing={1}
          >
            <Grid item xs={12} md={6} lg={4}>
              <InputField
                name="username"
                form={form}
                label="Tên tài khoản *"
                type="text"
                disabled={false}
              />
            </Grid>
            <Grid item xs={12} md={6} lg={4}>
              <InputField
                name="password"
                form={form}
                label="Mật khẩu *"
                type="text"
                disabled={false}
              />
            </Grid>
            <Grid item xs={12} md={6} lg={4}>
              <InputField
                name="thutu"
                form={form}
                label="Thứ tự"
                type="number"
                disabled={false}
              />
            </Grid>

            <Grid item xs={12} md={12} lg={12} style={{textAlign:"center"}}>
              <Button
                color="primary"
                variant="contained"
                disabled={isSubmitting}
                type="submit"
              >
                <AddIcon />
                <span>Thêm mới</span>
              </Button>
            </Grid>
          </Grid>
        </fieldset>
      </form>
    </Box>
  );
};

FormAddUser.propTypes = {};

export default FormAddUser;
