import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Box, Button, Grid } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';



const TableUser = ({userList, page, onClickOpenDialogDelete, onClickOpenDialogEditUser}) => {
  return (
    <Box mt={2}>
    <TableContainer>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="left" style={{fontWeight: "bold", fontSize: "16px"}}>STT</TableCell>
            <TableCell align="left" style={{fontWeight: "bold", fontSize: "16px"}}>Tên tài khoản</TableCell>
            <TableCell align="left" style={{fontWeight: "bold", fontSize: "16px"}}>Mật khẩu</TableCell>
            <TableCell align="right" style={{fontWeight: "bold", fontSize: "16px"}}>Thao tác</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {userList.map((user, index) => (
            <TableRow
              key={user._id}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {(page - 1) * 10 + 1 + index}
              </TableCell>
              <TableCell align="left" style={{fontWeight: "bold"}}>{user.username}</TableCell>
              <TableCell align="left">{user.password}</TableCell>
              <TableCell align="right">
                <Button variant="contained" color="success" style={{marginRight: "12px"}} onClick={()=> onClickOpenDialogEditUser(user)}>
                  <EditIcon style={{fontSize: '20px', marginRight: '4px'}}/> Sửa
                </Button>
                <Button variant="contained" color="error" onClick={()=> onClickOpenDialogDelete(user._id)}>
                  <DeleteOutlineIcon style={{fontSize: '20px', marginRight: '4px'}}/> Xóa
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </Box>
  );
}

export default TableUser